import React, { Component } from 'react'

import List from "./List"

class Apps extends Component {
    constructor(props) {
        super(props);
        this.state = {  
            items:[],
            currentItem:{
                text:"",
                key:""
            }
        }
        this.handleChange=this.handleChange.bind(this)
        this.addItem=this.addItem.bind(this)
        this.setUpdate=this.setUpdate.bind(this)
        this.deleteItem=this.deleteItem.bind(this)
    }

      handleChange(e){
        this.setState({
           currentItem:{
               text:e.target.value,
               key:Date.now()
           } 
        })
    }

     addItem(e){
      e.preventDefault();
      const newItem=this.state.currentItem
      
      if(newItem.text!=="")
      {
          const items=[...this.state.items, newItem]
          this.setState({
              items:items,
              currentItem:{
                  text:"",
                  key:""
              }
          })
      }  console.log(newItem)
    }

    setUpdate(text,key){
        const items=this.state.items
        items.map(item=>{
            if(item.key===key){
                item.text=text
            }
        })
        this.setState({
            items:items
        })
    }


     deleteItem(key){
        const filterItem=this.state.items.filter(item=>
            item.key!==key)
            
            this.setState({
                items:filterItem
            })
    }

    render() { 
        return (
            <div className="App">
            <header className="container mt-5">
            <form>
            <div className="form-group">
              <h3>To-Do List</h3>
              <input onChange={this.handleChange} type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Item To Add In The List"/>
              <small className="form-text text-muted">Added Item Will Show Below...</small>
            </div> 
            <button type="submit" onClick={this.addItem} className="btn btn-lg btn-primary">Submit</button>
            </form>
            </header>
            <List items={this.state.items} updateHandle={this.setUpdate} HandleDel={this.deleteItem}/>
           </div>
          );
    }
}
 
export default Apps;
