import React from 'react'
import FlipMove from "react-flip-move"

function List(props) {
    const items=props.items
    const listItem=items.map(item=>{
        return( 
        
       <div className="container">
       <center>
       <input type="text" className="alert alert-primary w-75 form-control-lg mt-3" rows="2" onChange={(e)=>{props.updateHandle(e.target.value,item.key)}}  value={item.text} />
          <span onClick={()=>props.HandleDel(item.key)} className="badge badge-primary ml-5 p-3 badge-pill">X</span>
      </center>
      </div>
        )}

    )
    return (
      <FlipMove duration={500} easing="ease-in">
      <div>{listItem}</div>
      </FlipMove>
    )
}

export default List
